import interfaceurl from '@/utils/interface.js'
import authPage from '@/components/authorization-page.vue' //引入授权窗体
import navigationBar from '@/components/navigation-bar.vue' //引入自定义导航栏
//引入store的内容
import {
	mapState,
	mapMutations,
	mapActions
} from 'vuex'
export default {
	components: {
		authPage,
		navigationBar
	},
	computed: mapState(['userInfos']),
	data() {
		return {
			//设置导航栏样式
			navigationBarStyle: {
				background: '#0071CF',
				fontColor: '#FFFFFF',
				iconColor: '#FFFFFF',
				iconText: '我的' //导航栏文字
			},
			detail: {},
			userInfo: {}
		}
	},
	watch:{
		userInfos(val) {
			//用户的授权信息存本地，全局变量是为了第一次授权时快速响应
			if(val) {
				this.userInfo = uni.getStorageSync('userInfo')
			}
		}
	},
	onLoad() {
		
	},
	onShow() {
		this.userInfo = this.userInfos.nickName ? this.userInfos : uni.getStorageSync('userInfo')
	},
	methods: {
		getUserInfo() {
			let that = this;
			interfaceurl.checkAuth(interfaceurl.showDetail, {}, false).then((res) => {
				that.userInfo = res.data
			});
		}
	}
}
